<template>
  <div>
    <b-card>
      <div slot="header">
        <h3 class="card-title">
          
        </h3>
      </div>
      <div class="form-group">
        <label class="form-label">
           []
        </label>
        <input v-model="data.panel_url" type="text" class="form-control" />
      </div>
      <div class="form-group">
        <label class="form-label">
          <a href="https://rasp.baidu.com/doc/install/panel.html#load-balance" target="_blank"></a> Agent  [ "" ]
        </label>
        <textarea
          type="text"
          class="form-control"
          v-model="data.agent_urls_text"
          rows="5"
        />
      </div>
      <div slot="footer">
        <b-button variant="primary" @click="saveData">
          
        </b-button>
      </div>
    </b-card>
    <b-card>
      <div slot="header">
        <h3 class="card-title">
          
        </h3>
      </div>     
      <p><strong></strong></p>      
      <ul>
        <li v-for="x in ['', '', '', '', '', '']">{{x}}</li>
      </ul>
      <div slot="footer">
        <b-button variant="danger" @click="removeLogs">
          
        </b-button>
      </div>
    </b-card>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "PanelSettings",
  data() {
    return {
      data: {
        panel_url: "",
        agent_url: [],
        agent_urls_text: ""
      }
    };
  },
  computed: {
    ...mapGetters(["current_app"])
  },
  mounted: function() {
    this.loadData();
  },
  methods: {
    loadData(data) {
      this.request.post("v1/api/server/url/get", {}).then(res => {
        this.data = res;
        this.data.agent_urls_text = res.agent_urls.join("\n");
      });
    },
    parseAgentURL: function() {
      var tmp = [];
      this.data.agent_urls_text.split("\n").forEach(function(item) {
        item = item.trim();
        if (item.length) {
          tmp.push(item);
        }
      });

      return tmp;
    },
    saveData: function() {
      var data = {
        panel_url: this.data.panel_url,
        agent_urls: this.parseAgentURL()
      };
      return this.request.post("v1/api/server/url", data).then(() => {
        alert("");
      });
    },
    removeLogs: function() {
      if (! confirm('')) {
        return
      }

      var data = {
        app_id: this.current_app.id
      }

      return this.request.post("v1/api/server/clear_logs", data).then(() => {
        alert("");
      });
    }
  }
};
</script>



