//  null/undefined/
export function trimSplit(data, sep) {
  var tmp = data.replace(/\s/g, '').split(sep)
  return tmp.filter(a => a)
}

export function convertToInt(data) {
  var tmp = []
  data.forEach (function (row) {
    tmp.push(parseInt(row))
  })

  return tmp.filter(a => a)
}

