export function validateRegex(value) {
  var error = false
  try {
    new RegExp(value)
  } catch (e) {
    error = ':' + e.toString()
  }

  return error
}

export function block_status2name(status) {
  return status_types[status] || status
}

export function attack_type2name(id) {
  if (id == 'webshell') {
    return 'WebShell '
  }

  if (id == 'xss') {
    return 'XSS '
  }

  return attack_types[id] || id
}

export const request = axios.create({
  baseURL:
    process.env.NODE_ENV === 'production'
      ? '/'
      : 'http://10.14.117.55:8080/',
  timeout: 8000
})
request.interceptors.request.use(
  config => {
    if (process.env.NODE_ENV !== 'production') {
      config.headers['X-OpenRASP-Token'] =
        '9256a3555fbd4f24f7a2ba915a32261ab4c720fc'
    }
    return config
  },
  error => {
    console.error(error)
    Promise.reject(error)
  }
)
request.interceptors.response.use(
  response => {
    const res = response.data
    if (res.status !== 0) {
      if (res.status === 401) {
        Cookie.set('RASP_AUTH_ID', null)
        if (router.currentRoute.name !== 'login') {
          router.push({
            name: 'login',
            query: {
              redirect: location.href
            }
          })
        }
      } else {
        alert(response.config.url + ' : ' + res.status + ' - ' + res.description)
      }
      return Promise.reject(res)
    } else {
      return res.data
    }
  },
  error => {
    alert('HTTP :  ' + error.response.status)
    console.error(error)
    return Promise.reject(error)
  }
)

export function download_file(name, data) {
  var blob  = new Blob([data], { type: 'application/octet-stream' })
  var link  = document.createElement('a')
  link.href = window.URL.createObjectURL(blob)
  link.download = name
  link.click()
}


