<script>

export default {
    name: 'baseline_params',
    data: function () {
        return {
            data: {
                policy_params: {}
            }
        }
    },
    methods: {
        setData: function (data) {
            this.data = data

            // v1.0 weblogic  policy_params 
            if (! data['policy_params']) {
                data['policy_params'] = {}
            }

            // v1.2  params.stack 
            if (! data.stack_trace && data['policy_params'].stack)
            {
                data.stack_trace = data['policy_params'].stack.join("\n")
            }
        }
    }
}
</script>




